.button {
  border: none;
  background-color: #10448c;
  font-weight: bold;
  line-height: 28px;
  color: #fff;
  padding: 0.5rem 1.25rem;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  vertical-align: top;
}

.button.outline {
  background-color: #fff;
  box-shadow: inset 0 0 0 3px #10448c;
  color: #10448c;
}

.button.hasIconPrevious {
  padding-left: 0.75rem;
}

.button.hasIconNext {
  padding-right: 0.75rem;
}

.icon {
  width: 28px;
  height: 28px;
}

.icon.iconPrevious {
  margin-right: 0.25rem;
}

.icon.iconNext {
  margin-left: 0.25rem;
}
