.wrapper {
  display: flex;
  flex-direction: column;
  position: relative;
  margin-bottom: 2rem;
}

.label {
  font-weight: bold;
  display: inline-flex;
  flex-direction: column;
  margin-bottom: 0.5rem;
}

.labelSubtext {
  font-weight: normal;
  font-size: 0.875rem;
  margin-top: 0.5rem;
}

.requiredMarker {
  color: red;
}

.selectedItem {
  background: white;
  border: 1px solid grey;
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-weight: bold;
  padding: 0.75rem;
  position: relative;
}

.selectButton {
  background-color: #404040;
  width: 50px;
  height: 100%;
  color: white;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  right: 0;
  position: absolute;
}

.selectButtonIcon {
  width: 2.4rem;
  height: 2.4rem;
}

.menu {
  margin: 0;
  padding: 0;
  width: 100%;
  overflow-y: scroll;
  background-color: #eee;
  list-style: none;
  max-height: 5.5rem;
  position: absolute;
  top: 100%;
  z-index: 1;
}

.selectItem {
  font-size: 0.875rem;
  padding: 0.25rem 0.75rem;
}

.selectItem.highlighted {
  background-color: #bde4ff;
}
